#_2YZJT {
  display: grid;
  align-items: center;
  margin-left: 0!important;
  margin-right: 0!important;
}

#_2YZJT > ._2fnJs {
  background: transparent;
  box-shadow: none;
  width: 100%;
  margin: 0 auto;
}

#_2YZJT > ._2fnJs > span > h2 {
  font-size: 4rem;
}

#_2YZJT > ._2fnJs > p {
  font-size: 1.5rem;
}

#_2YZJT > ._2fnJs, 
#_2YZJT > ._2fnJs > span > h2, 
#_2YZJT > ._1bIDH > h1  {
  color: var(--font-color);
}

#_2YZJT > ._1bIDH {
  background: var(--bg-color-2);
  width: 100%;
  margin: 0 auto;
  position: relative;
  display: block;
  border-radius: .2rem;
  padding: 3em 0;
}

#_2YZJT > ._1bIDH > ._7R9-n {
  text-align: center;
  position: relative;
  display: block;
  font-size: 3em;
  color: var(--font-color);
}

#_2YZJT > ._1bIDH > span {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
}

#_2YZJT > ._1bIDH > span > span {
  width: 110px;
}

#_2YZJT > ._1bIDH > span > span > svg {
  height: 120px;
  width: 120px;
  opacity: .8;
  transform: scale(.6);
  transform-origin: center;
  transition: all 300ms ease-in-out;
}

#_2YZJT > ._1bIDH > span > span > svg:hover {
  transform: scale(.8);
  opacity: 1;
}

/* to desktop */

@media (min-width: 768px) { 
  #_2YZJT {
    margin-left: auto!important;
    margin-right: auto!important;
  }

  #_2YZJT > ._2fnJs { 
    width: 80%;
  }
}